.menu__button {
  position: absolute;
  top: 16px;
  right: 16px;
  min-height: 40px;
  min-width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;

  margin: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  outline: none;
}

@media (min-width: 768px) {
  .menu__button {
    display: none;
  }
}

.menu__button:hover,
.menu__button:focus {
  background-color: rgba(0, 0, 0, 0.1);
}

.menu-container {
  position: fixed;
  // display: flex;
  // flex-direction: column;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding: 48px 40px;
  background-color: white;
  z-index: 999;

  transform: translateX(100%);
  transition: transform 250ms ease-in-out;
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu__button {
  position: absolute;
  top: 16px;
  right: 16px;
  color: white;
}

.header__menu_mobile {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 38px;
}

.mobile-menu__link {
  display: block;
  margin-bottom: 32px;
  font-weight: 500;
  font-size: 28px;
  line-height: 47px;
  letter-spacing: 0.02em;
  color: black;

  &:hover {
    color: blue;
  }
  @media screen and (min-width: 480px) and (max-width: 767px) {
    font-size: 40px;
  }
}
.mobile-menu {
  margin-bottom: auto;
}
.mobile-menu__link---bottom {
  margin-bottom: 0;
}
.mobile-menu__contact {
  margin-bottom: 64px;
}
.mobile-current {
  color: blue;
}
.mobile-menu__email {
  font-size: 24px;
  line-height: 1.71;
  font-weight: 500;
  letter-spacing: 0.02em;
  color: gray;
}
.mobile-menu__socials {
  display: flex;
  flex-wrap: wrap;
  row-gap: 10px;
  color: blue;
}
.mobile-menu__item {
  padding-right: 10px;
  margin-right: 10px;
  border-right: 1px solid rgba(33, 33, 33, 0.2);

  &:last-child {
    border: none;
    margin-right: 0;
  }
}
